<script>
import Modal from "../../../views/modal/Modal.vue";
import axios from "axios";
import moment from "moment"


let dependentInformation = {
	components: {
		Modal
	},
	props: {
		member_id: [String, Number],
		customer_id: [String, Number]
	},
	data() {
		return {
			// --- Date options ---
			formats: {
				input: ["DD/MM/YYYY"],
				data: ["DD/MM/YYYY"]
			},
			//---------------------
			empSelectorActive: {
				value: 0,
				text: ""
			},
			editEmployeeProfile: false,
			addDependentInfo: false,
			editRemoveEmpInfo: false,
			remove_step_active: 'remove-emp',
			removeBackBtn: false,
			editReplaceDependentInfo: false,
			inNetworkClaimSummaryModal: false,
			editDependentInfo: false,
			showInNetwork: false,
			showOutNetwork: false,
			spendingTypeOpt: 'medical',
			step_active: 1,
			showTimeVisitDropdown: false,
			showDaytimeOption: false,
			showTimeOption: false,
			showSetupAccountModal: false,
			showClaimTypeListOption: false,
			showMemberListOption: false,
			showRenewModal: false,
			showManageCapPerVisit: false,
			showSmsUpdateNotify: false,
			selected_emp_details_opt: 0,
			editCreditAllocationOpt: 'medical',
			editCreditAllocationTypeOpt: 'add',
			showEmpCreditsPlan: false,
			starDateDetails: {
				startDate: undefined,
			},
			showShortTermSelector: false,

			// Depedents Global Variables
			dependent_arr: {},
			toEdit: {},
			global_toRemoveDep: {},
			global_withdrawEmployeeModal: false,
			global_toReplaceDep: {},
			// -------------------------
		};
	},
	created() {
		this.healthPartnerViewStatus = this.$route.name;

		this.onLoad();
	},
	methods: {
		formatDate(date, from, to) {
			if (date != null) {
				return moment(date, from).format(to);
			}
		},
		selectHealthPartnerView(opt) {
			this.healthPartnerViewStatus = opt;
			this.$router.push({ name: opt });
		},
		showEmpSelectorInfo(value, text) {
			this.empSelectorActive.value = value;
			this.empSelectorActive.text = text;
		},
		showEditEmp() {
			this.editEmployeeProfile = this.editEmployeeProfile == false ? true : false;
		},
		showAddDependent() {
			this.addDependentInfo = this.addDependentInfo == false ? true : false;
		},
		showRemoveEmp() {
			this.editRemoveEmpInfo = this.editRemoveEmpInfo == false ? true : false;
		},
		showReplaceDependent(list) {
			this.editReplaceDependentInfo = this.editReplaceDependentInfo == false ? true : false;

			if	(list)	{
				this.global_toReplaceDep.member_id = list.member_id;
			}
		},
		showRemoveDependent(list) {
			this.global_withdrawEmployeeModal = this.global_withdrawEmployeeModal == false ? true : false;

			if	(list)	{
				this.global_toRemoveDep = list;
				this.global_toRemoveDep.expiry_date = new Date();
			}
		},
		showEditDependent(list) {
			this.editDependentInfo = this.editDependentInfo == false ? true : false;

			if(list) {
				console.log(list);
				list.dob = new Date(list.dob);
				list.plan_start = new Date(list.plan_start);
				this.toEdit = list;
			}
		},
		removeEmployeeBtn(data) {
			let x = data;

			if (x === "back") {
				if (this.remove_step_active == 'remove-opt') {
					this.removeBackBtn = false;
					this.remove_step_active = 'remove-emp';
				}

				if (this.remove_step_active == 'replace-emp') {
					this.remove_step_active = 'remove-opt';
				}

				if (this.remove_step_active == 'health-spending-summary') {
					this.remove_step_active = 'remove-opt';
				}

				if (this.remove_step_active == 'health-spending-account') {
					this.remove_step_active = 'health-spending-summary';
				}
			}

			if (x === "next") {

				if (this.remove_step_active == 'remove-emp') {
					this.removeBackBtn = true;
					this.remove_step_active = 'remove-opt';

				} else if (this.remove_step_active == 'remove-opt') {

					if (this.emp_details_replace) {
						this.remove_step_active = 'replace-emp';
					}

					if (this.emp_details_reserve) {
						this.remove_step_active = 'health-spending-summary';
					}

					if (this.emp_details_remove) {
						this.remove_step_active = 'health-spending-summary';
					}

				} else if (this.remove_step_active == 'replace-emp') {
					this.remove_step_active = 'health-spending-summary';

				} else if (this.remove_step_active == 'health-spending-summary') {
					this.remove_step_active = 'health-spending-account';
				}

			}


		},
		changeRemoveOption(opt) {
			this.emp_details_replace = false;
			this.emp_details_reserve = false;
			this.emp_details_remove = false;

			if (opt === 1) {
				this.emp_details_replace = true;
			}
			if (opt === 2) {
				this.emp_details_reserve = true;
				console.log('2 ni siya');
			}
			if (opt === 3) {
				this.emp_details_remove = true;
				console.log('3 ni siya');
			}
		},
		inNetworkSubmit() {
			this.inNetworkClaimSummaryModal = this.inNetworkClaimSummaryModal == false ? true : false;
		},
		toggleShowInNetwork(data) {
			let x = data;
			if (x === "in-network") {
				this.showInNetwork = true;
			} else if (x === "cancel") {
				this.showInNetwork = false;
			}
		},
		toggleShowOutNetwork(data) {
			let x = data;
			if (x === "out-network") {
				this.showOutNetwork = true;
			} else if (x === "cancel") {
				this.showOutNetwork = false;
			}
		},
		editInNetworkOpt() {
			this.showOutNetwork = true;
		},
		claimTypeListOption() {
			this.showClaimTypeListOption = this.showClaimTypeListOption == false ? true : false;
		},
		memberListOption() {
			this.showMemberListOption = this.showMemberListOption == false ? true : false;
		},
		setSpendingType(opt) {
			this.spendingTypeOpt = opt;
		},
		clickedTimeVisitDropdown() {
			this.showTimeVisitDropdown = this.showTimeVisitDropdown == false ? true : false;
		},
		clickedTimeOption() {
			this.showTimeOption = this.showTimeOption == false ? true : false;
		},
		clickedDaytimeOption() {
			this.showDaytimeOption = this.showDaytimeOption == false ? true : false;
		},
		empDetailsOutNetworkNextBackBtn(data) {
			let x = data;
			if (x === "next") {
				if (this.step_active == 1) {
					this.step_active = 2;
				} else if (this.step_active == 2) {
					this.step_active = 3;
				}
			}
			if (x === "back") {
				if (this.step_active == 2) {
					this.step_active = 1;
				} else if (this.step_active == 3) {
					this.step_active = 2;
				}
			}
		},
		//SETTINGS BUTTON FOR SHOWING MODAL
		selectedEmpDetailsSettingsClicked(value, data) {
			let y = value;
			let x = data;

			if (y == 0) {
				if (x === "setup-reset-account") {
					this.showSetupAccountModal = true;
				} else if (x === "cancel") {
					this.showSetupAccountModal = false;
				}
			}

			if (y == 1) {
				if (x === "renew-plan") {
					this.showRenewModal = true;
				} else if (x === "cancel") {
					this.showRenewModal = false;
				}
			}

			if (y == 2) {
				if (x === "manage-visit") {
					this.showManageCapPerVisit = true;
				} else if (x === "cancel") {
					this.showManageCapPerVisit = false;
				}
			}

			if (y == 3) {
				if (x === "sms-update-notify") {
					this.showSmsUpdateNotify = true;
				} else if (x === "cancel") {
					this.showSmsUpdateNotify = false;
				}
			}

		},
		selectedEmpDetailsToggleOpt(opt) {
			this.selected_emp_details_opt = opt;
		},
		creditAllocationOption(opt) {
			this.editCreditAllocationOpt = opt;
		},
		creditAllocationTypeFunction(opt) {
			this.editCreditAllocationTypeOpt = opt;
		},
		openEmpCreditsPlan(data) {
			let x = data;
			if (x === "credits-plans") {
				this.showEmpCreditsPlan = true;
			} else if (x === "cancel") {
				this.showEmpCreditsPlan = false;
			}
		},
		shortTermRadioBtn(data) {
			let x = data;
			if (x === "short-term") {
				this.showShortTermSelector = true;
			} else if (x === "standard-one-year") {
				this.showShortTermSelector = false;
			}
		},

		// Jaz methods
		updateDependent(toEdit) {
			let update_dependent_details = `${axios.defaults.serverUrl}/company/update_dependent_details`;

			let data  = {
				member_id: toEdit.member_id,
				fullname: toEdit.fullname,
				dob: moment(toEdit.dob).format('YYYY-MM-DD'),
				relationship: toEdit.relationship,
				plan_start: toEdit.plan_start,
			}

			if(this.checkForm_editDep(data)) {
				this.showLoading();

				data.plan_start = moment(data.plan_start).format('YYYY-MM-DD')
				
				axios.put(update_dependent_details, data)
				.then(res => {
					// Log the data to the console
					// You would do something with both sets of data here
					console.log(res);
					this.hideLoading();
					
					if (res.data.status == true) {
						this.$swal('Success', res.data.message, 'success')
							.then(res1 => {
								// this.toEdit = {};
								// this.editDependentInfo = false;
							})
					} else {
						this.hideLoading();
						this.$swal("Error!", res.data.message, "error");
					}
					// this.hideLoading();
				}).catch(err => {
					this.hideLoading();
					this.errorHandler(err);
				});
			}
		},
		checkForm_editDep(data) {
			this.error_editDep = [];

			if (!data.fullname) {
				this.error_editDep.push("Name.");
			}
			if (!data.dob) {
				this.error_editDep.push("Birthday.");
			}
			if (!data.relationship) {
				this.error_editDep.push("Relationship.");
			}
			if (!data.plan_start) {
				this.error_editDep.push("Start Date.");
			}

			if (!this.error_editDep.length) {
				return true;
			} else {
				console.log(this.error_editDep);
				let new_error = [];
				this.error_editDep.map(value => {
					new_error.push(`<span class="block p-1 text-red-500 text-center w-1/2 mx-auto my-0">${value}</span>`);
				});
				this.$swal(
					'Required',
					new_error.join('\n\n'),
					'warning'
				);

			}
		},
		// ----------

		// API METHODS

		getEmployeeDependents() {
			let get_employee_dependents = `${axios.defaults.serverUrl}/company/get_employee_dependents?customer_id=${this.customer_id}&employee_id=${this.member_id}`;

			axios.get(get_employee_dependents)
				.then(res => {
					// Log the data to the console
					// You would do something with both sets of data here
					console.log(res);
					if (res.data.status == true) {
						this.dependent_arr = res.data.dependents;
						// this.$emit('FromEmployee', { from_employee: this.employee_info });
						console.log(this.dependent_arr);
						this.hideLoading();
					}
					// this.hideLoading();
				}).catch(err => {
					this.hideLoading();
					this.errorHandler(err);
				});
		},

		_deleteDependent_	(result)	{
			console.log(result);
			this.showLoading();
			const URL = `${axios.defaults.serverUrl}/company/remove_dependent_account`;

			let _data	=	{
				member_id:	this.global_toRemoveDep.member_id,
				expiry_date:	moment(this.global_toRemoveDep.expiry_date).format('YYYY-MM-DD'),
				refund_status:	result,
			}

			axios.post(URL,	_data)
				.then(res => {
					// Log the data to the console
					// You would do something with both sets of data here
					console.log(res);
					if	(res.status == 201) {
						this.hideLoading();
						this.$swal('Success', res.data.message, 'success')
							.then(swalRes => {
								this.$emit('FromEmployee', { from_employee: this.employee_info });
							})
					}else {
						this.hideLoading();
						this.$swal('Warning!', res.data.message, 'warning');
					}
					// this.hideLoading();
				}).catch(err => {
					this.hideLoading();
					this.errorHandler(err);
				});
		},

		_deleteDependentSwal_	()	{

			this.$swal({
				title: 'Confirm!',
				text: "Do you want to create a refund for this dependent",
				type: 'warning',
				showCloseButton: true,
				showCancelButton: true,
				confirmButtonColor: '#25306c',
				cancelButtonColor: '#C1C1C1',
				cancelButtonText: 'No Refund.',
				confirmButtonText: 'Yes, Make a Refund.',
				reverseButtons: true,
				allowOutsideClick: true,
			}).then((swalResult) => {
				if (swalResult.value) {
					this._deleteDependent_(swalResult.value);
				} else if	(swalResult.dismiss ==	'cancel')	{
					swalResult.value	=	false;
					this._deleteDependent_(swalResult.value);
				}
			})
		},

		_replaceDependet_	()	{
			const URL = `${axios.defaults.serverUrl}/company/replace_dependent_account`;

			let _data	=	{
				member_id:	this.global_toReplaceDep.member_id,
				fullname:	this.global_toReplaceDep.fullname,
				dob:	this.global_toReplaceDep.dob,
				relationship:	this.global_toReplaceDep.relationship,
				expiry_date:	this.global_toReplaceDep.plan_start,
				plan_start:	this.global_toReplaceDep.plan_start,
			}

			if	(this.checkForm_editDep(_data))	{

				_data.dob	=	moment(this.global_toReplaceDep.dob).format('YYYY-MM-DD');
				_data.expiry_date = moment(this.global_toReplaceDep.plan_start).format('YYYY-MM-DD');
				_data.plan_start = moment(this.global_toReplaceDep.plan_start).format('YYYY-MM-DD');

				axios.post(URL,	_data)
				.then(res => {
					// Log the data to the console
					// You would do something with both sets of data here
					console.log(res);
					if	(res.status == 201) {
						this.hideLoading();
						this.$swal('Success', res.data.message, 'success')
							.then(swalRes => {
								this.$emit('FromEmployee', { from_employee: this.employee_info });
							})
					}else {
						this.hideLoading();
						this.$swal('Warning!', res.data.message, 'warning');
					}
					// this.hideLoading();
				}).catch(err => {
					this.hideLoading();
					this.errorHandler(err);
				});
			}
		},

		onLoad() {
			this.showLoading();
			axios.all([
				// API ARRAY
				this.getEmployeeDependents(),
			]).then(res => {
				// Log the data to the console
				// You would do something with both sets of data here
				// console.log(res);
				let res_len = res.length;
				res.map((value, index) => {
					if (index == res.length - 1) {
						// this.hideLoading();
					}
				});
			}).catch(error => {
				// if there's an error, log it
				console.log(error);
				this.hideLoading();
			});
		},
	}
}

export default dependentInformation
</script>

